.sidebar {
    position: absolute;
    top: 0;
    left: -100%;
    width: 317px;
    height: 100%;
    padding: 4rem 0 0 4.375rem;
    background-color: #fff;
    filter: drop-shadow(0 0 2px rgba(29, 33, 38, 0.102))
    drop-shadow(0 0 40px rgba(29, 33, 38, 0.129));
    z-index: 1;
    transition: all 0.35s;
}

.sidebar.open {
    left: 0;
}

.background {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 1;
    transition: all 0.35s;
}

.nav {
    display: flex;
    flex-direction: column;
    gap: 2.75rem;
}

.nav a {
    font-size: 1.375rem;
    font-weight: 600;
    line-height: 1.22;
    text-decoration: none;
    color: #000;
    transition: all 0.25s;
}

.nav a:hover {
    color: #58acff;
}

.nav a.active {
    color: #0059b2;
    text-decoration: underline;
}
.close {
    border: 0;
    background-color: transparent;
    width: 24px;
    height: 24px;
    cursor: pointer;
    position: absolute;
    right: 24px;
    top: 16px;
}

